.button1{
    width: 100px;
    height: 40px;
    background-color: lightgray;
    position: absolute;
    bottom: 0px;
    left: 180px;
    color: white;
    border-top-left-radius:150px;
    border-bottom-left-radius: 150px;
}
.button2{
    width: 100px;
    height: 40px;
    background-color: gray;
    position: absolute;
    bottom: 0px;
    left: 270px;
    color: white;
    border-top-right-radius:150px;
    border-bottom-right-radius: 150px;
}
.button3{
    width: 60px;
    height: 50px;
    position: absolute;
    bottom: 0;
    left: 120px;
    background-color: #fff;
    border: none;
}
.button3 >i{
    position: absolute;
    left: 22px;
    top: 8px;
    color: grey;
}
.button3 >p{
    margin-top: 27px;
    font-size: 9px;
    color: grey;
}